// Phones & Handhelds
@media (max-width: 480px) {

	.header {
		& select {
			width: 80%;
		}
	}
	//Adding padding per bootstrap fully fluid layout guidance
	.sessions {
		padding: 0 15px;
	}

	.collapsed {
		// 3 lines
		display: block;
		display: -webkit-box;
		max-width: 100%;
		height: 43px;
		margin: 0 auto;
		font-size: 14px;
		line-height: 1;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		overflow: hidden;
		text-overflow: ellipsis;
	}
}