@import 'base/colors-mixins';
/* CHECKBOXES */

// https://bootsnipp.com/snippets/ZkMKE

.category {
	margin: 10px 0;
}

.checkbox {
	// padding-left: 20px;
	// margin-bottom: 20px;
}

.checkbox label {
	display: inline-block;
	position: relative;
	// padding-left: 5px;
}

// checkbox background color
.checkbox label::before {
	content: "";
	display: inline-block;
	position: absolute;
	width: 17px;
	height: 17px;
	left: 0;
	margin-left: -20px; // needed for checkmark
	border: 1px solid #cccccc; // checkbox border
	border-radius: 3px;
	background-color: #fff;
	// -webkit-transition: border 0.15s ease-in-out, color 0.15s ease-in-out;
	// -o-transition: border 0.15s ease-in-out, color 0.15s ease-in-out;
	// transition: border 0.15s ease-in-out, color 0.15s ease-in-out;
}

// checkbox disable border color
.checkbox label::after {
	display: inline-block;
	position: absolute;
	width: 16px;
	height: 16px;
	left: 0;
	top: 0;
	margin-left: -20px;
	padding-left: 3px; // checkmark positioning
	padding-top: 1px; // checkmark positioning
	font-size: 11px;
	color: #555555;
}

.checkbox input[type="checkbox"] {
	opacity: 0;
}

.checkbox input[type="checkbox"]:focus + label::before {
	outline: thin dotted;
	outline: 5px auto -webkit-focus-ring-color;
	outline-offset: -2px;
}

.checkbox input[type="checkbox"]:checked + label::after {
	font-family: 'FontAwesome';
	content: "\f00c";
}

.checkbox input[type="checkbox"]:disabled + label {
	opacity: 0.65;
}

.checkbox input[type="checkbox"]:disabled + label::before {
	background-color: #eeeeee;
	cursor: not-allowed;
}

.checkbox.checkbox-circle label::before {
	border-radius: 50%;
}

.checkbox.checkbox-inline {
	margin-top: 0;
}

// checkbox background color
.checkbox-primary input[type="checkbox"]:checked + label::before {
	background-color: $secondary-color;
	border-color: $secondary-color;
}

// checkbox checkmark
.checkbox-primary input[type="checkbox"]:checked + label::after {
	color: #fff;
}